import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    classePlano: null,
    plano: null,
    select: null,
  },
  mutations: {
    SET_CLASSE_PLANO(state, value) {
      state.classePlano = value;
    },
    SET_PLANO(state, value) {
      state.plano = value;
    },
    SET_SELECT(state, value) {
      state.select = value;
    },
  }
})
