<template>
    <!-- APARECE SOMENTE SE UM PLANO FOR SALVO NO VUEX -->
  <div id="resultado" class="resultado-container" v-if="plano">
      <h1 class="titulo">
          FaleMais{{plano}}
      </h1>
      <div class="resultado-plano">
        <p >
            De <b>{{classePlano.origem}}</b> para  <b>{{classePlano.destino}}</b>: 
        </p>
        <!-- APARECE SOMENTE SE UM CUSTO POR MINUTO FOR CALCULADO COM SUCESSO -->
        <p v-if="classePlano.custoPorMinuto()">
            sem FaleMais {{semFaleMais}} <br class="mobile" /> <b>com FaleMais {{comFaleMais}}</b> 
        </p>
        <!-- APARECE SOMENTE SE UM CUSTO POR MINUTO NÃO FOR CALCULADO COM SUCESSO -->
        <p v-else>
            Não temos essa opção de plano
        </p>
      </div>
  </div>
</template>

<script>
export default {

    computed:{
        //RESTORNA A CLASSE SALVA NO VUEX
        classePlano() {
            return this.$store.state.classePlano  
        },
        //RETORNA O PLANO SALVO NO VUEX
        plano(){
            return this.$store.state.plano
        },
        //RETORNA O CALCULO DOS MINUTOS SEM O PLANO
        semFaleMais(){
            return this.calculaPlanoSemFaleMais()
        },
         //RETORNA O CALCULO DOS MINUTOS COM O PLANO
        comFaleMais(){
            return this.calculaPlanoComFaleMais()
        },
    },
    methods: {
        calculaPlanoSemFaleMais(){
            return "$ "+this.classePlano.custoPorMinuto() * parseFloat(this.classePlano.tempo)+",00"
        },
        calculaPlanoComFaleMais(){
            let precoNormal = this.classePlano.custoPorMinuto() * parseFloat(this.classePlano.tempo)
            let minutosExcedentes = parseInt(this.classePlano.tempo) - parseInt(this.plano)
            let dezPorCento = (this.classePlano.custoPorMinuto() * parseFloat(minutosExcedentes) )* 10 / 100
            let precoAteAtingirPlano = this.classePlano.custoPorMinuto() * parseFloat(this.plano)

            if(precoNormal <= precoAteAtingirPlano){
                return "R$ 0,00"
            }else{
                let resul = String(precoNormal - precoAteAtingirPlano + dezPorCento)
                if(resul.includes('.')){
                    return "$ "+ resul + "0"
                }else{
                    return "$ "+ resul + ",00"
                }
                // return precoNormal - precoAteAtingirPlano + dezPorCento
            }
        },
    },
    watch: {
    classePlano() {
      this.calculaPlanoSemFaleMais()
    }
  }
}
</script>

<style scoped lang="scss">
@import '../assets/sass/_cores.scss';

.resultado-container{
    margin: 40px auto;
    width: 70%;
    text-align: initial;
    padding-left: 48px;
    @media (max-width: 768px) {
            padding-left: 0;
            width: 90%;
        }
    p{
        font-size: 22px;
        font-weight: 700;
        font-family: ui-sans-serif;

        b{
            color: $roxo;
        }
    }
    .resultado-plano{
        display: flex;
        @media (max-width: 768px) {
            flex-direction: column;
            width: 90%;
        }
        p{
            margin-right: 25px;
            @media (max-width: 768px) {
                margin: 0;
            }
        }
    }
    .mobile{
        display: none;
        @media (max-width: 768px) {
               display: block;
            }
    }
}
</style>
