<template>
  <div class="container-plano">
    <h1>Os planos são:</h1>
    <!-- A LINHA A BAIXO APARECE APENAS SE NÃO TIVER PLANO SELECIONADO -->
    <h3 :class="{ inativo: algumPlanoAtivo }">Selecione um dos planos para fazer uma simulação.</h3>
    
    <ul class="lista">
        <li @click="planoAtivo(30)">
            <div class="item_plano">
                <img :src="imagem1" alt="" /> 
                <p>FaleMais 30</p>
            </div>
            <div class="mostra_plano" :class="{ plano_ativo_30: plano == 30 }" id="plano30">
                <h1>
                    {{tituloPlano}}
                </h1>
                <p>
                    Selecione a origem, o destino e o tempo de ligação para fazer uma simulação
                </p>
                <div class="selectes">
                    <Select
                        @selecionou="selecionaOrigem"
                        :Placeholder="origem"
                        :Opcoes="origens"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                    <Select
                        @selecionou="selecionaDestino"
                        :Placeholder="destino"
                        :Opcoes="destinos"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                    <Select
                        @selecionou="selecionaTempo"
                        :Placeholder="tempo"
                        :Opcoes="tempos"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                </div>
            </div>
        </li>
        <li @click="planoAtivo(60)">
            <div class="item_plano">
                <img :src="imagem2" alt="" /> 
                <p>FaleMais 60</p>
            </div>
            <div class="mostra_plano" :class="{ plano_ativo_30: plano == 60 }" id="plano60">
                <h1>
                    {{tituloPlano}}
                </h1>
                <p>
                    Selecione a origem, o destino e o tempo de ligação para fazer uma simulação
                </p>
                <div class="selectes">
                    <Select
                        @selecionou="selecionaOrigem"
                        :Placeholder="origem"
                        :Opcoes="origens"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                    <Select
                        @selecionou="selecionaDestino"
                        :Placeholder="destino"
                        :Opcoes="destinos"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                    <Select
                        @selecionou="selecionaTempo"
                        :Placeholder="tempo"
                        :Opcoes="tempos"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                </div>
            </div>
        </li>
        <li @click="planoAtivo(120)">
            <div class="item_plano">
                <img :src="imagem3" alt="" /> 
                <p>FaleMais 120</p>
            </div>
            <div class="mostra_plano" :class="{ plano_ativo_30: plano == 120 }" id="plano120">
                <h1>
                    {{tituloPlano}}
                </h1>
                <p>
                    Selecione a origem, o destino e o tempo de ligação para fazer uma simulação
                </p>
                <div class="selectes">
                    <Select
                        @selecionou="selecionaOrigem"
                        :Placeholder="origem"
                        :Opcoes="origens"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                    <Select
                        @selecionou="selecionaDestino"
                        :Placeholder="destino"
                        :Opcoes="destinos"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                    <Select
                        @selecionou="selecionaTempo"
                        :Placeholder="tempo"
                        :Opcoes="tempos"
                        Chave="titulo"
                        v-scroll-to="'#play'"
                    />
                </div>
            </div>
        </li>
    </ul>
     <div id="play" class="play" :class="{ margin_play: tituloPlano != '' }" @click="play" v-scroll-to="'#resultado'">PLAY</div>
  </div>
</template>

<script>
//IMPORTAÇÕES DE IMAGENS
import plano_30 from "../assets/img/plano-30.png"
import plano_60 from "../assets/img/plano-60.png"
import plano_120 from "../assets/img/plano-120.png"
//IMPORTAÇÕES DE COMPONENTES
import Select from '../components/Select.vue'

export default {
    components: {
        Select
    },
    name: 'Planos',
    data() {
    return {
      imagem1: plano_30,
      imagem2: plano_60,
      imagem3: plano_120,
      plano: 0,
      origem: 'origem',
      destino: 'destino',
      tempo: 'tempo',
      algumPlanoAtivo: false,
      tituloPlano: "",
      origens: [
        { titulo: "011"},
        { titulo: "016"},
        { titulo: "017"},
        { titulo: "018"}
      ],
      destinos: [
        { titulo: "011"},
        { titulo: "016"},
        { titulo: "017"},
        { titulo: "018"}
      ],
      tempos: [
        { titulo: "20"},
        { titulo: "80"},
        { titulo: "100"},
        { titulo: "200"}
      ],
    };
  },
  methods:{
      planoAtivo(plano){
          switch (plano) {
              case 30:
                  this.tituloPlano = "Pano FaleMais 30"
                  this.plano = plano
                  this.algumPlanoAtivo = true
                  break;
              case 60:
                  this.tituloPlano = "Pano FaleMais 60"
                  this.plano = plano
                  this.algumPlanoAtivo = true
                  break;
              case 120:
                  this.tituloPlano = "Pano FaleMais 120"
                  this.plano = plano
                  this.algumPlanoAtivo = true
                  break;
          
              default:
                  break;
          }
      },
      selecionaOrigem(e) {
        this.origem = e.titulo
    },
      selecionaDestino(e) {
        this.destino = e.titulo
    },
      selecionaTempo(e) {
        this.tempo = e.titulo
    },
    play(){

        this.criaClass(this.origem, this.destino, this.tempo)
        //RESETA AS VARIAVEIS DO SELECT
        this.origem = 'origem'
        this.destino = 'destino'
        this.tempo = 'tempo'
    },
    criaClass(ori, des, tem){
        class Plano {
            constructor(origem, destino, tempo) {
                this.origem = origem;
                this.destino = destino;
                this.tempo = tempo;
            }
            //RETORNA O CUSTO POR MINUTO DE ACORDO COM ORIGEM E DESTINO ESCOLHIDO
            custoPorMinuto() { 
                let custo;
                if(this.origem == "011" && this.destino == "016"){
                    custo = parseFloat(1.90)
                }else if(this.origem == "011" && this.destino == "017"){
                    custo = parseFloat(1.70)
                }else if(this.origem == "011" && this.destino == "018"){
                    custo = parseFloat(0.90)
                }else if(this.origem == "016" && this.destino == "011"){
                    custo = parseFloat(2.90)
                }else if(this.origem == "017" && this.destino == "011"){
                    custo = parseFloat(2.70)
                }else if(this.origem == "018" && this.destino == "011"){
                    custo = parseFloat(1.90)
                }
                return custo;
            }
        }
        let novoPlano = new Plano(ori, des, tem);
        //SALVA A CLASE NO VUEX
        this.$store.commit("SET_CLASSE_PLANO", novoPlano)
        //SALVA O PLANO NO VUEX
        this.$store.commit("SET_PLANO", this.plano)
        this.$store.commit("SET_SELECT", null)
        
    }
  }
}
</script>

<style scoped lang="scss">
@import '../assets/sass/_cores.scss';

.container-plano{
    margin: 40px auto;
    width: 70%;
    text-align: initial;
    padding-left: 48px;
    @media (max-width: 768px) {
            width: 100%;
            margin: 0 auto;
            padding-left: 10px;
        }
    h3{
        font-size: 14px;
        margin-top: -25px;
        font-family: ui-sans-serif;
        color: $azul;
        position: absolute;
    }
    .inativo{
        display: none;
    }
    .play{
        width: 42px;
        background: #000;
        color: $branco;
        padding: 17px 6px;
        border-radius: 27px; 
        cursor: pointer;
    }
    .margin_play{
        margin-top: 226px;
        @media (max-width: 768px) {
            margin-top: 0;
        }
    }
}
ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    justify-content: space-between;
    position: relative;
    @media (max-width: 768px) {
            flex-direction: column;
        }
}
li {
    
    img{
        width: 55px;
        margin-right: 20px;
    }
    .item_plano{
        margin:20px 0 0;
        display: flex;
        align-items: center;
        cursor: pointer;
    }
    p{
        font-size: 22px;
        font-weight: 700;
        font-family: ui-sans-serif;
    }
    .mostra_plano{
        position: absolute;
        width: 100%;
        height: auto;
        top: 90px;
        left: 0;
        margin-top: 20px;
        display: none;
        @media (max-width: 768px) {
            position: relative;
            width: 100%;
            height: auto;
            top: 0;
            left: 0;
            margin-top: 20px;
            display: none;
        }
        .selectes{
            display: flex;
            width: 100%;
            justify-content: space-between;
            @media (max-width: 768px) {
                flex-direction: column;
                align-items: center;
            }
        }
    }
    .plano_ativo{
        &_30{
            display: block !important;
        }
        &_60{
            display: block !important;
        }
        &_120{
            display: block !important;
        }
    }
}

</style>
